// --- Portal popover helpers (defined outside component, as they are pure functions/hooks) ---
function usePortalPopover(isOpen, btnRef) {
  const [pos, setPos] = useState({ top: 0, left: 0 });
  useEffect(() => {
    if (isOpen && btnRef.current) {
      const rect = btnRef.current.getBoundingClientRect();
      setPos({
        top: rect.bottom + window.scrollY + 4,
        left: rect.left + window.scrollX,
      });
    }
  }, [isOpen, btnRef]);
  return pos;
}

// --- Firestore Readiness Utility Function ---
const checkFirestoreReadiness = (db, userId, operation = 'Firestore operation') => {
  console.log(`[DEBUG] ${operation} - Checking Firestore readiness...`);
  console.log(`[DEBUG] db value:`, db);
  console.log(`[DEBUG] db type:`, typeof db);
  console.log(`[DEBUG] db === null:`, db === null);
  console.log(`[DEBUG] db === undefined:`, db === undefined);
  
  // Check if db is null/undefined
  if (!db) {
    console.error(`${operation}: Firestore instance (db) is null or undefined`);
    return { ready: false, error: 'Firestore is not initialized. Please refresh the page.' };
  }

  // Check if db is a valid Firestore instance
  if (typeof db !== 'object') {
    console.error(`${operation}: Firestore instance (db) is not an object:`, db);
    return { ready: false, error: 'Firestore is not properly initialized. Please refresh the page.' };
  }

  // Check if db has the app property
  if (!db.app) {
    console.error(`${operation}: Firestore instance (db) is missing app property:`, db);
    console.log(`[DEBUG] db keys:`, Object.keys(db));
    return { ready: false, error: 'Firestore is not properly initialized. Please refresh the page.' };
  }

  // Check if db.app is a valid Firebase app
  if (typeof db.app !== 'object' || !db.app.name) {
    console.error(`${operation}: Firestore app is not valid:`, db.app);
    return { ready: false, error: 'Firestore app is not properly initialized. Please refresh the page.' };
  }

  // Check if userId is available
  if (!userId) {
    console.error(`${operation}: User ID is null or undefined`);
    return { ready: false, error: 'User not authenticated. Please log in to use this feature.' };
  }

  // Check if userId is a string
  if (typeof userId !== 'string' || userId.trim() === '') {
    console.error(`${operation}: User ID is not a valid string:`, userId);
    return { ready: false, error: 'Invalid user authentication. Please log in again.' };
  }

  console.log(`[DEBUG] ${operation} - Firestore readiness check passed`);
  return { ready: true, error: null };
};

// --- Safe Firestore Operation Wrapper ---
const safeFirestoreOperation = async (db, userId, operation, firestoreFunction, errorMessage = 'Firestore operation failed') => {
  const readiness = checkFirestoreReadiness(db, userId, operation);
  if (!readiness.ready) {
    throw new Error(readiness.error);
  }

  try {
    return await firestoreFunction();
  } catch (error) {
    console.error(`${operation} failed:`, error);
    throw new Error(`${errorMessage}: ${error.message}`);
  }
};

