    if (!song) return lyricsThemes[0];
    // Simple hash from title+artist
    let str = (song.title || '') + (song.artist || '');
    let hash = 0;
    for (let i = 0; i < str.length; i++) hash = str.charCodeAt(i) + ((hash << 5) - hash);
