  // --- ORDER FIX: Declare sortTracks and getPlaylistTracks first ---
  const sortTracks = useCallback((list, sortByOption, sortOrderOption) => {
    if (!Array.isArray(list) || list.length === 0) return [];
    const sortedList = [...list].sort((a, b) => {
      let valA, valB;
      if (sortByOption === 'custom' && selectedPlaylist) {
        const playlistTrackIds = selectedPlaylist.trackIds || [];
        const indexA = playlistTrackIds.indexOf(a.id);
        const indexB = playlistTrackIds.indexOf(b.id);
        return indexA - indexB;
      }
      switch (sortByOption) {
        case 'title':
          valA = a.title?.toLowerCase() || '';
          valB = b.title?.toLowerCase() || '';
          break;
        case 'artist':
          valA = a.artist?.toLowerCase() || '';
          valB = b.artist?.toLowerCase() || '';
          break;
        case 'dateAdded':
          valA = a.timestamp instanceof Date ? a.timestamp.getTime() : (a.timestamp ? new Date(a.timestamp).getTime() : 0);
          valB = b.timestamp instanceof Date ? b.timestamp.getTime() : (b.timestamp ? new Date(b.timestamp).getTime() : 0);
          break;
        default:
          return 0;
      }
      if (typeof valA === 'string' && typeof valB === 'string') {
        return sortOrderOption === 'asc' ? valA.localeCompare(valB) : valB.localeCompare(valA);
      } else if (typeof valA === 'number' && typeof valB === 'number') {
        return sortOrderOption === 'asc' ? valA - valB : valB - valA;
      }
      return 0;
    });
    return sortedList;
  }, [selectedPlaylist]);

