  const errorMessageBoxClasses = 'bg-gradient-to-r from-red-600 to-pink-600 text-white rounded-2xl backdrop-blur-xl';
  const playlistCardClasses = 'bg-white/5 backdrop-blur-xl border-white/10 hover:bg-white/10 text-white rounded-3xl shadow-2xl';
