  const deleteButtonClasses = 'text-red-400 hover:text-red-300 hover:bg-red-900/30 rounded-full';

  // --- Advanced Audio Controls ---
  const handleAudioEnded = useCallback(() => {
    setIsPlaying(false);
    let currentList = [];
    if (activeView === 'library') {
      currentList = sortedTracks;
    } else if (activeView === 'playlist-detail' && selectedPlaylist) {
        currentList = sortedPlaylistTracks;
    } else {
      currentList = searchResults;
    }
    const currentIndex = currentList.findIndex(t => t.id === currentTrack?.id || t.url === currentTrack?.url);
    let nextTrack = null;
    if (currentIndex > -1 && currentIndex < currentList.length - 1) {
      nextTrack = currentList[currentIndex + 1];
    } else if (currentList.length > 0) {
      nextTrack = currentList[0];
    }
    if (nextTrack) {
      if (activeView === 'search') {
        // Inline the logic of handlePlaySearchResult for search results
        const existingTrack = tracks.find(t => t.url === nextTrack.url);
        if (existingTrack) {
          showMessage(`"${existingTrack.title}" is already in your library. Playing now.`, 'info');
          setCurrentTrack(existingTrack);
          setIsPlaying(true);
          return;
        }
        setIsLoading(true);
        setPlayingOrDownloadingId(nextTrack.url);
        (async () => {
          try {
            const response = await fetch(`${BACKEND_URL}/download-mp3`, {
              method: 'POST',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({ url: nextTrack.url, quality: audioQuality })
            });
            if (!response.ok) {
              const errorData = await response.json();
              throw new Error(errorData.message || `Backend download failed: HTTP status ${response.status}`);
            }
            const data = await response.json();
            if (!data.audioUrl) {
              throw new Error("No audio URL received from the backend after download. It might not be a playable track.");
            }
            const newTrackRef = await addDoc(collection(db, `artifacts/${FIRESTORE_APP_ID}/users/${userId}/tracks`), {
              title: data.title || nextTrack.title,
              artist: data.artist || nextTrack.artist,
              audioUrl: data.audioUrl,
              thumbnail: data.thumbnail || nextTrack.thumbnail,
              url: nextTrack.url,
              timestamp: new Date().toISOString()
            });
            const downloadedTrack = {
              id: newTrackRef.id,
              title: data.title || nextTrack.title,
              artist: data.artist || nextTrack.artist,
              audioUrl: data.audioUrl,
              thumbnail: data.thumbnail || nextTrack.thumbnail,
              url: nextTrack.url,
              timestamp: new Date().toISOString()
            };
            showMessage(`"${downloadedTrack.title}" downloaded and added to your library!`, 'success');
            setCurrentTrack(downloadedTrack);
            setIsPlaying(true);
          } catch (error) {
            console.error("Download or playback failed:", error);
            showMessage(`Failed to download or play "${nextTrack.title}": ${error.message}. Please check backend and track availability.`, 'error');
            setCurrentTrack(null);
            setIsPlaying(false);
          } finally {
            setIsLoading(false);
            setPlayingOrDownloadingId(null);
          }
        })();
    } else {
        setCurrentTrack(nextTrack);
        setIsPlaying(true);
        }
      } else {
        setCurrentTrack(null);
        setIsPlaying(false);
      }
  }, [activeView, currentTrack, searchResults, selectedPlaylist, sortedTracks, sortedPlaylistTracks, db, userId, showMessage, tracks, BACKEND_URL, audioQuality]);

